.pswp img {
  max-width: none;
  object-fit: contain;
}

.chat-online {
  color: #34ce57;
}

.chat-offline {
  color: #e4606d;
}

.message-container {
  background-color: #fff;
  margin: auto;
  width: 73%;
  height: 1000px;
}

.chat-messages {
  display: flex;
  flex-direction: column-reverse;

  height: 800px;
  overflow-y: scroll;
}

.chat-message-left,
.chat-message-right {
  display: flex;
  flex-shrink: 0;
}

.chat-message-left {
  margin-right: auto;
}

.chat-message-right {
  flex-direction: row-reverse;
  margin-left: auto;
}
.py-3 {
  padding-top: 1rem !important;
  padding-bottom: 1rem !important;
}
.px-4 {
  padding-right: 1.5rem !important;
  padding-left: 1.5rem !important;
}
.flex-grow-0 {
  flex-grow: 0 !important;
}
.border-top {
  border-top: 1px solid #dee2e6 !important;
}

.message-form {
  position: absolute;
  width: 100%;
  display: flex;
}
